<template>
    <div class="hangqing">
        <headerBar :title="headerNavData"></headerBar>
        <div class="mysearch12">
          <yd-search v-model="search" 
            :on-submit="submitHandler" 
            :on-cancel="cancelHandler"
            :placeholder="$t('placeholder.search')"></yd-search>
        </div>
        <div class="pro_list" style="margin:0">
            <yd-flexbox class="pro_list_item" v-for="item in list">
                <div  class="pro_list_img">
                    <img :src="item.currIconPath">
                </div>
                <yd-flexbox-item  class="pro_list_price">
                    <span>{{item.currName}}</span>
                    <!-- <span>{{item.currName}}/{{item.relyCurrency}}</span> -->
                </yd-flexbox-item>
                <div  class="pro_list_ps" v-if="item.currName_b">
                    <div class="switch" :class="{'switchbj':item.isSelected==1}">
                      <div @click="swtc(item)" class="yuan" :class="{'yuanbj':item.isSelected==1}"></div>
                    </div>
                </div>
            </yd-flexbox>
        </div>
        <div v-if="pagenum > 15">
          <div class="loaddata" v-if="loadtext == $t('data.loadmore')" @click="loadList()">{{loadtext}}</div>
          <div class="loaddata" v-if="loadtext == $t('toast.nodata')">{{loadtext}}</div>
        </div>
        
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('header.tianjiazichan'),
         right:false,
         bj:'#2c94ed'
      },
      sw:false,
      search:'',
      page:1,
      pagesize:20,
      pagenum:0,
      list:[],
      loadtext:this.$t('data.loadmore')
    }
  },
  methods:{
    swtc(item){
      var list = this.list
      for (var i = 0; i < list.length; i++) {
        if (item.id == list[i].id) {
          if (list[i].isSelected == 0) {
            list[i].isSelected = 1
            this.deloradd(1,item.id)
          }else{
            list[i].isSelected = 0
            this.deloradd(3,item.id)
          }
        }
        
      }
    },
    submitHandler(value) {
        this.page = 1
        this.list=[]
        this.loadList()
    },
    cancelHandler(){
      this.search=''
      this.page = 1
      this.list=[]
      this.loadList()
    },
    loadList() {
      this.openloading(this.$t('toast.loding'))
      var that = this;
      this.axios({
        url:this.Url + 'ShowCurrency.ashx',
        method:'post',
        params: {req:6,key:this.search,publickey:getCookie("publickey"),pageindex:this.page,pagecount:this.pagesize}
      }).then(function (res) {
        that.closeloading()
          var _list = res.data.data;
          if (res.data.data.length == 0) {
            that.loadtext = that.$t('toast.nodata')
              that.$dialog.toast({
                  mes: that.$t('toast.nodata'),
                  timeout: 1500
              });
              return;
          }else{
            for (var i = 0; i < _list.length; i++) {
              _list[i].currIconPath = that.imgUrl + _list[i].currIconPath
              if (_list[i].currName == "BDK" || _list[i].currName == "ETH") {
                _list[i].currName_b = false
              }else{
                _list[i].currName_b = true
              }
            }
            that.list = [...that.list,..._list];
            that.pagenum = res.data.totalCount
            that.page++;
          }
          
      });
    },
    deloradd(req,id){
      var that = this
      this.axios({
        url:this.Url + 'ShowCurrency.ashx',
        method:'post',
        params: {req:req,publickey:getCookie("publickey"),currencyId:id}
      }).then(function (res) {
          if (res.data.success) {
            setCookie("Currency_list","")
            that.Toast(res.data.msg)
          }else{
            that.Toast(res.data.msg)
          }
      });
    }
  },
  created(){
    this.loadList()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hangqing{
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.yuan{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #c5c5c5;
  position: absolute;
  top: -5px;
  left: 0;
}
.loaddata{
  width: 100%;
  text-align: center;
  background: #fff;
  height: 45px;
  line-height: 45px;
  color: #888;
  font-size: .22rem;
  border-top: 1px solid #f1f1f1
}
.yuanbj{
  background: #278eed!important;
  left: 22px!important;
}
.switchbj{
  background: #278eed!important;
}
.switch{
  height: 10px;
  width: 40px;
  background: #e0e0e0;
  position: relative;
  border-radius: 10px;
}
.red{
  color: red;
}
.green{
  color: green
}
.hangqing{
  padding-top: 1.9rem
}
.pro_list_price{
  text-align: left;
  padding-left: .2rem
}
.pro_list_item{
    border-bottom: 1px solid #f4f4f4;
    background: #fff;
    padding: .2rem;
}

.pro_list_price span:nth-child(1){
    display: block;
    font-size: .3rem;
    font-weight: bolder;
    color: #777;
}
.pro_list_price span:nth-child(2){
    display: block;
    font-size: .22rem;
    color: #999;
}
.pro_list_img{
  border: 2px solid #dfebff;
  border-radius: 50%;
  width: .7rem;
  height: .7rem;
  position: relative;
}
.pro_list_img img{
    /*width: .6rem;
    height: .6rem;
    border-radius: 50%;
    display: inline-block;
    float: left;*/
    width: 16px;
    height: 16px;
    border-radius: 50%;
    display: inline-block;
    float: left;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -8px;
    margin-top: -8px;
}
.pro_list_pz{
  text-align: center;
}
.pro_list_pz span:nth-child(1){
    font-size: .3rem;
    font-weight: bold;
    color: #5aa5e8;
    display: block;
}
.pro_list_pz span:nth-child(2){
    font-size: .24rem;
    color: #939292;
}
.pro_list_ps{
  padding-left: .6rem; 
  font-size: .25rem
}
.mysearch12{
  position: fixed;
  top: .9rem;
  left: 0;
  width: 100%;
  z-index: 99;
}
</style>



